@import url(https://fonts.googleapis.com/css?family=Squada+One&display=swap);
* {
  box-sizing: border-box; }

body {
  font-family: 'Squada One', cursive;
  font-size: 18px; }

.container {
  display: flex;
  flex-direction: column;
  max-width: 1080px;
  flex-wrap: wrap; }

.left-screen {
  background-color: #D72113;
  max-width: 500px; }

.left-screen-content {
  min-height: 590px;
  padding: 50px 30px 30px 30px;
  margin: 0 20px 20px 20px;
  border-bottom: 1px solid #2A2B26;
  border-left: 1px solid #2A2B26;
  border-right: 1px solid #2A2B26; }

.left-screen-header {
  display: flex;
  min-height: 120px; }

.lights-container {
  display: flex;
  width: 50%;
  border-bottom: 1px solid #2A2B26;
  margin-left: 20px;
  margin-top: 20px; }

.left-screen-footer {
  display: flex;
  flex-direction: row; }

.left-screen-footer-left-col {
  width: 20%;
  margin-right: 30px; }

.left-screen-footer-middle-col {
  width: 50%;
  margin-right: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.left-screen-footer-right-col {
  width: 30%; }

.left-screen-footer-row {
  display: flex;
  flex-direction: row; }

.right-screen {
  display: flex;
  flex-direction: column;
  background-color: #D72113;
  max-width: 500px; }

.right-screen-content {
  min-height: 570px;
  padding: 30px;
  margin: 0 20px 20px 20px;
  border-bottom: 1px solid #2A2B26;
  border-left: 1px solid #2A2B26;
  border-right: 1px solid #2A2B26; }

.main-display-wrapper {
  position: relative;
  padding: 30px 30px 0 30px;
  background-color: #fff;
  margin-bottom: 30px; }

.main-display-cut {
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 40px solid transparent;
  border-left: 40px solid #D72113;
  z-index: 1; }

.main-display-footer {
  height: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between; }

.misc-button-container {
  display: flex;
  flex-direction: row; }

.misc-button-left-col {
  width: 50%;
  display: flex;
  flex-direction: row; }

.misc-button-right-col {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-end; }

.misc-button-row {
  display: flex;
  flex-direction: row;
  margin-bottom: 15px; }

.white-grid-button {
  background-color: #fff;
  width: 84px;
  height: 60px;
  border-top: 3px solid #2A2B26;
  border-left: 3px solid #2A2B26; }
  .white-grid-button:nth-child(2n) {
    border-right: 3px solid #2A2B26;
    border-bottom: 3px solid #2A2B26; }
  .white-grid-button:nth-child(n+1) {
    border-bottom: 3px solid #2A2B26; }

.sprite-button {
  width: 50px;
  height: 50px;
  background-color: #2A2B26;
  border-radius: 50%;
  box-shadow: 2px 3px 0px 0px rgba(0, 0, 0, 0.2); }

.hinge-container {
  display: none;
  width: 60px;
  flex-direction: column;
  border-right: 1px solid #2A2B26;
  background-color: #D72113; }

@media only screen and (min-width: 960px) {
  .hinge-container {
    display: flex; }
  .container {
    flex-direction: row;
    flex-wrap: nowrap; }
  .left-screen, .right-screen {
    width: 50%; } }

.hinge-top {
  height: 60px;
  border-bottom: 1px solid #2A2B26;
  border-left: 1px solid #2A2B26; }

.hinge-middle {
  flex: 1 1;
  border-left: 1px solid #2A2B26; }

.hinge-bottom {
  height: 60px;
  border-top: 1px solid #2A2B26;
  border-left: 1px solid #2A2B26; }

.hinge-bottom-lid {
  height: 20px;
  border-top: 1px solid #2A2B26;
  border-left: 1px solid #2A2B26; }

.type-display-container {
  display: flex;
  flex-direction: row; }

.type-display-wrapper {
  text-align: center;
  width: calc(50% - 10px);
  color: #fff;
  margin-right: 20px;
  background-color: #2A2B26;
  padding: 15px 30px; }
  .type-display-wrapper:nth-child(2n) {
    margin-right: 0; }

.type-display {
  padding: 15px 10px;
  border: 1px solid #fff;
  border-radius: 30px; }

.number-display-wrapper {
  background-color: #164614;
  padding: 15px;
  flex: 1 1; }

.number-display {
  color: #fff;
  text-align: center;
  font-size: 20px;
  text-transform: capitalize; }

.sprite-container {
  display: flex;
  flex-direction: column;
  background-color: #2A2B26;
  min-height: 280px; }

.sprite-middle {
  flex: 1 1;
  display: flex;
  flex-direction: row; }

.sprite-cycle {
  min-width: 60px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .sprite-cycle.top {
    min-height: 50px; }
  .sprite-cycle.bottom {
    min-height: 50px; }
  .sprite-cycle.left {
    flex-direction: column; }
  .sprite-cycle.right {
    flex-direction: column; }

.sprite-wrapper {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center; }

.sprite-wrapper img {
  width: 160px;
  height: 160px; }

.secondary-display-wrapper {
  color: #fff;
  background-color: #2A2B26;
  padding: 15px;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 150px;
  flex-wrap: wrap; }

.statistic-wrapper {
  width: calc(100%/3);
  text-align: center; }

.height-weight-wrapper {
  width: 50%;
  text-align: center; }

.mic-light-holder {
  width: 80px;
  height: 80px;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 3px 3px 0px 0px rgba(0, 0, 0, 0.2); }

.mic-light {
  position: relative;
  width: 70px;
  height: 70px;
  background-color: #368EB6;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid #368EB6; }

.mic-light:before {
  content: '';
  background: #00dbff;
  width: 60px;
  height: 60px;
  position: absolute;
  border-radius: 55%;
  top: -7px;
  left: -7px; }

.mic-light:after {
  content: "";
  width: 13px;
  height: 13px;
  position: absolute;
  background: #fff;
  border-radius: 50%;
  top: 15px;
  left: 13px; }

.dpad-wrapper {
  width: 120px;
  height: 120px;
  position: relative; }

.dpad-btn {
  width: 40px;
  height: 40px;
  background: #2A2B26;
  position: absolute; }

.dpad-middle {
  top: 40px;
  left: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; }

.dpad-middle:after {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  width: 7px;
  height: 7px;
  background-color: #000; }

.dpad-middle-finger {
  border: 1px solid #000;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  left: 5px; }

.dpad-up {
  top: 0;
  left: 40px;
  border-right: 7px solid #000; }

.dpad-right {
  top: 40px;
  right: 0;
  border-right: 7px solid #000;
  border-bottom: 7px solid #000; }

.dpad-down {
  bottom: 0;
  left: 40px;
  border-right: 7px solid #000;
  border-bottom: 7px solid #000; }

.dpad-left {
  top: 40px;
  left: 0;
  border-bottom: 7px solid #000; }

.indicator-light {
  position: relative;
  width: 20px;
  height: 20px;
  margin: 0 10px;
  box-shadow: 2px 2px 0px 0px rgba(0, 0, 0, 0.2);
  border-radius: 50%; }
  .indicator-light.red {
    background-color: #D72113;
    -webkit-animation: red-light-transition 1s linear 0s infinite normal;
            animation: red-light-transition 1s linear 0s infinite normal; }
  .indicator-light.yellow {
    background-color: #D6920B;
    -webkit-animation: yellow-light-transition 1s linear 0.5s infinite normal;
            animation: yellow-light-transition 1s linear 0.5s infinite normal; }
  .indicator-light.green {
    background-color: #164614;
    -webkit-animation: green-light-transition 1s linear 1.5s infinite normal;
            animation: green-light-transition 1s linear 1.5s infinite normal; }

.indicator-light:after {
  content: "";
  width: 6px;
  height: 6px;
  position: absolute;
  background: #fff;
  border-radius: 50%;
  top: 3px;
  left: 3px; }

@-webkit-keyframes red-light-transition {
  from {
    background-color: #D72113; }
  to {
    background-color: #f44336; } }

@keyframes red-light-transition {
  from {
    background-color: #D72113; }
  to {
    background-color: #f44336; } }

@-webkit-keyframes yellow-light-transition {
  from {
    background-color: #D6920B; }
  to {
    background-color: #ffe01c; } }

@keyframes yellow-light-transition {
  from {
    background-color: #D6920B; }
  to {
    background-color: #ffe01c; } }

@-webkit-keyframes green-light-transition {
  from {
    background-color: #164614; }
  to {
    background-color: #3dba61; } }

@keyframes green-light-transition {
  from {
    background-color: #164614; }
  to {
    background-color: #3dba61; } }

.grid-button-container {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px; }

.grid-button {
  background: #368EB6;
  width: 20%;
  text-align: center;
  padding: 15px;
  border-top: 3px solid #2A2B26;
  border-left: 3px solid #2A2B26;
  height: 60px; }
  .grid-button:nth-child(5n) {
    border-right: 3px solid #2A2B26; }
  .grid-button:nth-child(n+6) {
    border-bottom: 3px solid #2A2B26; }

.confirm-button {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #D6920B;
  box-shadow: 2px 3px 0px 0px rgba(0, 0, 0, 0.2); }

.slim-button {
  width: 60px;
  height: 10px;
  background-color: #2A2B26;
  border: 1px solid #2A2B26;
  box-shadow: 2px 3px 0px 0px rgba(0, 0, 0, 0.2); }

.display-vent {
  width: 80px;
  height: 3px;
  background-color: #000;
  margin-bottom: 3px; }

.power-button {
  width: 20px;
  height: 20px;
  background-color: #D72113;
  border-radius: 50%;
  margin-left: 20px;
  box-shadow: 2px 2px 0px 0px rgba(0, 0, 0, 0.2); }

